@tailwind base;
@tailwind components;
@tailwind utilities;
::-webkit-scrollbar {
  width: 2px;
  background: red;
}

::-webkit-scrollbar-track {
  background: white;
}

::-webkit-scrollbar-thumb {
  background-color: skyblue;
  /* border: 3px solid transparent; */
  border-radius: 9px;
  background-clip: content-box;
}

@media print{
  .header{
    visibility: hidden;
  }

  .sidebar{
    visibility: hidden;
  }
}